<template>
  <div class>
    <div class="seat-labels">
      <div class="container">
        <div class="flexbox align-items-center">
          <img src="@/assets/icons/booked.svg" alt />
          <span>Booked</span>
        </div>
        <div class="flexbox align-items-center">
          <img src="@/assets/icons/available.svg" alt />
          <span>Available</span>
        </div>
        <div class="flexbox align-items-center">
          <img src="@/assets/icons/selected.svg" alt />
          <span>Selected</span>
        </div>
      </div>
    </div>

    <div class="container p-1 pos-relative">
      <!-- Loader -->
      <div
        class="bus-progress-loader flexbox align-items-center justify-center zindex-10"
        v-if="!busSeatLoadingComplete"
      >
        <div class="text-primary">
          <i class="fas fa-spinner fa-pulse fa-5x"></i>
        </div>
      </div>

      <!-- Loader -->
      <div
        class="loader bus-progress-loader flexbox align-items-center justify-center zindex-10"
        v-show="show_Loader"
      >
        <div class="text-primary">
          <i class="fas fa-spinner fa-pulse fa-5x"></i>
        </div>
      </div>
      <!--Loader-->

      <div
        class="title-wrapper with-icon pos-relative flexbox justify-center align-items-center p-t-xs p-b-xs mr-t-2 mr-b-2"
      >
        <span class="p-l-2 p-r-2 mobile-p-l-0 mobile-p-r-0 align-items-center zindex-5">
          <img src="@/assets/icons/bus-icon-blue.svg" alt="Book Bus" />
          <h3 class="title font-h-2 mobile-font-sm">Select your seats</h3>
        </span>
      </div>

      <div class="row seat-layout-row p-t-1 pos-relative">
        <div class="bus-seat-wrapper for-desktop p-b-2">
          <table border="0" cellpadding="2" cellspacing="1" class="bus-body w-100">
            <tr v-for="(row_, index) in  activeTableCols" :key="index">
              <td v-for="(col_, index) in activeTableRows" :key="index">
                <div v-for="(seat_, index) in seats" :key="index">
                  <div class="seat-item" v-if="row_ == seat_.seat_col && col_ == seat_.seat_row">
                    <input
                      type="checkbox"
                      :id="`seat-${seat_.seat_col}-${seat_.seat_row}`"
                      class="seat-checkbox"
                      @change="seatSelected(seat_, $event)"
                      v-bind:disabled="seat_.seat_number == 'Dr' || seat_.seat_status == 'reserved'"
                      :checked="isSeatSelected(seat_.seat_number)"
                    />
                    <label
                      :for="`seat-${seat_.seat_col}-${seat_.seat_row}`"
                      :title="`${seat_.seat_number} | ${seat_.seat_type} | ${seat_.price.price_kes_amount}`"
                    >
                      <span class="seat-text">{{seat_.seat_number}}</span>
                      <span class="armrest-right arm"></span>
                      <span class="armrest-left arm"></span>
                      <span class="backrest"></span>
                    </label>
                  </div>
                </div>
              </td>
            </tr>
          </table>
        </div>

        <div class="bus-seat-wrapper for-mobile p-b-2">
          <table border="0" cellpadding="2" cellspacing="1" class="bus-body w-100">
            <tr v-for="(row_, index) in activeTableRows" :key="index">
              <td v-for="(col_, index) in activeTableCols" :key="index">
                <div v-for="(seat_, index) in seats" :key="index">
                  <div class="seat-item" v-if="row_ == seat_.seat_row && col_ == seat_.seat_col">
                    <input
                      type="checkbox"
                      :id="`seat-mobile-${seat_.seat_col}-${seat_.seat_row}`"
                      class="seat-checkbox"
                      @change="seatSelected(seat_, $event)"
                      v-bind:disabled="seat_.seat_number == 'Dr' || seat_.seat_status == 'reserved'"
                      :checked="isSeatSelected(seat_.seat_number)"
                    />
                    <label :for="`seat-mobile-${seat_.seat_col}-${seat_.seat_row}`">
                      <span class="seat-text">{{seat_.seat_number}}</span>
                      <span class="armrest-right arm"></span>
                      <span class="armrest-left arm"></span>
                      <span class="backrest"></span>
                    </label>
                  </div>
                </div>
              </td>
            </tr>
          </table>
        </div>

        <div class="current-trip-details-panel w-33 mobile-w-100">
          <div class="inline-form-wrapper">
            <form
              method="post"
              class="inline-form"
              @submit.prevent="$emit('busSeatSelected', trip.boarding_point, trip.dropping_point, selected_seats)"
              action="#"
            >
              <div class="form-group p-0 mr-b-1">
                <label class id="boading-point">Boarding Point</label>
                <v-select
                  v-model="trip.boarding_point"
                  v-on:change="trip.boarding_point = ''"
                  class="modern-selector is-inline"
                  placeholder="Select Boading Point"
                  label="branch_name"
                  id="boading-point"
                  :options="trip.selected_bus.pickup_branches"
                  required
                ></v-select>
              </div>

              <div class="form-group p-0 mr-b-1">
                <label class id="dropping-point">Dropping Point</label>
                <v-select
                  v-model="trip.dropping_point"
                  v-on:change="trip.dropping_point = ''"
                  class="modern-selector is-inline"
                  placeholder="Select Dropping Point"
                  label="branch_name"
                  id="dropping-point"
                  :options="trip.selected_bus.dropoff_branches"
                  required
                ></v-select>
              </div>

              <div class="form-group pricing-total-wrapper">
                <div
                  class="row-item selected-seat-wrapper flexbox justify-between align-items-center p-b-1"
                >
                  <span class="label font-semibold">Selected Seats</span>
                  <span class="value font-bold">
                    <span
                      v-for="(selected_seat, index) in selected_seats"
                      :key="index"
                    >{{ selected_seat.seat_number }},</span>
                  </span>
                </div>
                <div
                  class="row-item total-amount-wrapper flexbox justify-between align-items-center p-b-1"
                >
                  <span class="label font-semibold">Total Amount</span>
                  <span class="value font-bold">KES {{ total_amount | formatCurrency }}</span>
                </div>
              </div>

              <!-- Show form feedback here -->
              <div
                class="form-group form-message text-center bg-white hidden"
              >Enter some form feedback here or error message</div>
              <!-- Show form feedback here -->

              <!-- Submit Form -->
              <div class="form-group">
                <button class="btn" id="continue-btn">CONTINUE</button>
              </div>
              <!-- /Submit Form -->
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/*Vuex Data & Actions*/
import { mapGetters, mapActions } from "vuex";

//Izi Toast
import izitoast from "izitoast";
import "izitoast/dist/css/iziToast.min.css";
import "izitoast/dist/js/iziToast.min.js";

export default {
  name: "AppBusSeats",
  data() {
    return {
      busSeatLoadingComplete: false,
      activeTableCols: 5,
      activeTableRows: 3,
      buseData: {},
      seats: null,
      max_seats: 6
    };
  },
  computed: mapGetters([
    "trip",
    "bus_seats",
    "selected_seats",
    "total_amount",
    "show_Loader"
  ]),
  methods: {
    ...mapActions([
      "getBusSeats",
      "getSelectedSeats",
      "removeUnSelectedSeats",
      "getTotalAmount",
      "getLockSeats"
    ]),
    isSeatSelected(seat_number) {
      return this.selected_seats.some(
        seat => seat["seat_number"] === seat_number
      );
    },
    seatSelected: function(seat_, event) {
      if (event.target.checked) {
        if (this.selected_seats.length >= this.max_seats) {
          event.target.checked = false;

          //ALert
          izitoast.warning({
            title: "Caution",
            message: "Sorry, maximum number of seats selected"
          });

          return false;
        }
        this.getSelectedSeats({ seat: seat_, is_return: false });
        this.getTotalAmount({ is_return: false });

        //Lock Seats
        this.getLockSeats({ seat_is_return: false, seat: seat_ });
      } else {
        this.removeUnSelectedSeats({ seat: seat_, is_return: false });
        this.getTotalAmount({ is_return: false });
      }
    },
    getBusDetailsRequest: function() {
      let self = this;
      var requestOptions = {
        method: "GET",
        redirect: "follow"
      };

      fetch(
        `http://193.200.241.91:8000/api/v2/schedules/${self.trip.selected_bus.schedule_id}/seats?town_from_id=${self.trip.town_from.id}&town_to_id=${self.trip.town_to.id}`,
        requestOptions
      )
        .then(response => {
          return response.json();
        })
        .then(data => {
          //Set bus seats to store
          this.getBusSeats({ seats: data, is_return: false });

          let _busData = data.bus_type;
          self.seats = data.seats;
          self.buseData = data.bus_type;
          self.activeTableCols = _busData.bus_type_cols;
          self.activeTableRows = _busData.bus_type_rows;
          self.busSeatLoadingComplete = true;
        })
        .catch(error => {
          //ALert
          izitoast.error({
            title: "Error",
            message: "Sorry, error occured!"
          });
          console.log(`Server error ${error}`);
        });
    }
  },
  mounted() {
    this.getBusDetailsRequest();
  }
};
</script>

