<template>
  <div class="p-t-2 w-60 mobile-w-100">
    <!-- Slider main container -->
    <div class="swiper-container">
      <!-- Additional required wrapper -->
      <div class="swiper-wrapper">
        <!-- Slides -->
        <div class="swiper-slide">
          <img
            srcset="@/assets/banner/banner-bus@2x.png 2x, @/assets/banner/banner-bus.png 1x"
            src="@/assets/banner/banner-bus.png"
            alt="Modern Banner"
            class="w-100 mr-b-1"
          />
          <div class="content mr-l-auto mr-r-auto mr-t-0 mr-b-0 p-1 mobile-w-100 mobile-w-100">
            <h2 class="title mr-0 mr-b-1">Welcome to Modern</h2>
            <p
              class="text mr-0 mr-b-1 w-60 mobile-w-100"
            >Experience <strong>luxury</strong> travel across Kenya, Uganda, Tanzania &amp; Rwanda.</p>

            <a href="#" class="link with-line font-semibold hidden">View Buses</a>
          </div>
        </div>
      </div>

      <div
        class="swiper-tools pos-relative flexbox justify-between align-items-center p-1 p-t-sm mr-auto"
      >
        <div class="swiper-pagination-wrapper">
          <div class="swiper-pagination pos-relative"></div>
        </div>

        <div class="swiper-button-wrapper flexbox align-items-center">
          <div class="swiper-button-prev pos-static auto-h mr-0 mr-r-1">
            <img src="@/assets/icons/banner-navigation-left.svg" alt="Navigation Icon Previous" />
          </div>
          <div class="swiper-button-next pos-static auto-h mr-0 mr-r-1">
            <img src="@/assets/icons/banner-navigation-right.svg" alt="Navigation Icon Next" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//Swiperjs
import Swiper from "swiper";
import "swiper/css/swiper.min.css";

export default {
  name: "Banner",
  mounted() {
    var homeSwiper = new Swiper(".swiper-container", {
      slidesPerView: "auto",
      autoHeight: true,
      pagination: {
        el: ".swiper-pagination",
        clickable: true
      },
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev"
      }
    });

    homeSwiper.init();
  }
};
</script>
