<template>
  <div class="page-wrapper buses-page">
    <!-- App Header -->
    <AppHeader layoutType="layout-2"></AppHeader>

    <section class="viewport-h-min-100">
      <!-- Type of Trips-->
      <div class="type-of-seats">
        <div class="container">
          <router-link to="/Buses" class="flexbox align-items-center" @click.native="showOnward">
            <span>
              ONWARD TRIP
              <i class="fas fa-arrow-alt-circle-right"></i>
            </span>
          </router-link>
          <router-link
            to="/Buses"
            class="flexbox align-items-center"
            @click.native="showReturn"
            v-if="trip.returning_on"
          >
            <span>
              RETURN TRIP
              <i class="fas fa-arrow-alt-circle-left"></i>
            </span>
          </router-link>
        </div>
      </div>
      <!--Type of Trips end-->

      <!-- Include AppTripFilter Componenet -->
      <AppTripFilter v-if="showAppTripFilter" />

      <!-- Include AppTripFilter Componenet -->
      <AppReturnTripFilter v-if="showAppReturnTripFilter" />

      <!-- Include AppLoader (Show Progress On Page) -->
      <AppLoader v-if="show_Loader" />

      <!-- Include AppBusResults Componenet  -->
      <AppBusResults v-if="showAppBusResults" @busSelected="busSelectedEvent" />

      <!-- Include AppReturnBusResults Componenet  -->
      <AppReturnBusResults v-if="showAppReturnBusResults" @busSelected="busSelectedEvent" />

      <!-- Include AppBusSeats Componenet  -->
      <AppBusSeats v-if="showAppBusSeats" @busSeatSelected="busSeatSelectedEvent" />

      <!-- Include AppBusSeats Componenet  -->
      <AppReturnBusSeats v-if="showAppReturnBusSeats" @busSeatSelected="busSeatSelectedEvent" />

      <!--Include AppPassangerForm Componenet -->
      <AppPassangerForm
        v-if="showAppPassangerForm"
        @passangerFormSubmitted="passangerFormSubmittedEvent"
        :show_Loader="show_Loader"
      ></AppPassangerForm>

      <!-- Include AppBusSeats Componenet  -->
      <AppTripReview v-if="showAppTripReview" @tripReviewConfirmed="tripReviewEvent" />

      <!-- Include AppPayment Componenet  -->
      <AppPayment v-if="showAppPayment" />
    </section>

    <!-- App Footer -->
    <AppFooter />

    <!-- App Footer -->
    <AppModal v-if="showModal" @close="showModal = false" @demoModalContinue="modalClosedEvent"></AppModal>
  </div>
</template>

<script>
/*Vuex Data & Actions*/
import { mapGetters, mapActions } from "vuex";

//Izi Toast
import izitoast from "izitoast";
import "izitoast/dist/css/iziToast.min.css";
import "izitoast/dist/js/iziToast.min.js";

//AppHeader component
import AppHeader from "@/components/AppHeader.vue";

//AppTripFilter component
import AppTripFilter from "@/components/AppTripFilter.vue";

//AppReturnTripFilter component
import AppReturnTripFilter from "@/components/AppReturnTripFilter.vue";

//AppTLoader component
import AppLoader from "@/components/AppLoader.vue";

//AppBusResults Componenet
import AppBusResults from "@/components/AppBusResults.vue";

//AppReturnBusResults Componenet
import AppReturnBusResults from "@/components/AppReturnBusResults.vue";

//AppBusSeats Componenet
import AppBusSeats from "@/components/AppBusSeats.vue";

//AppBusSeats Componenet
import AppReturnBusSeats from "@/components/AppReturnBusSeats.vue";

//AppPassangerForm component
import AppPassangerForm from "@/components/AppPassangerForm.vue";

//AppTripReview component
import AppTripReview from "@/components/AppTripReview.vue";

//AppPayment component
import AppPayment from "@/components/AppPayment.vue";

//AppFooter component
import AppFooter from "@/components/AppFooter.vue";

//AppModal component
import AppModal from "@/components/AppModal.vue";

export default {
  name: "Buses",
  components: {
    AppHeader,
    AppTripFilter,
    AppReturnTripFilter,
    AppLoader,
    AppBusResults,
    AppReturnBusResults,
    AppBusSeats,
    AppReturnBusSeats,
    AppPassangerForm,
    AppTripReview,
    AppPayment,
    AppFooter,
    AppModal
  },
  data() {
    return {
      showAppTripFilter: true,
      showAppReturnTripFilter: false,
      showAppLoader: false,
      showAppBusResults: true,
      showAppReturnBusResults: false,
      showAppBusSeats: false,
      showAppReturnBusSeats: false,
      showAppPassangerForm: false,
      showAppTripReview: false,
      showAppPayment: false,
      seats: null
    };
  },
  computed: mapGetters([
    "show_Loader",
    "trip",
    "selected_seats",
    "return_selected_seats",
    "passenger_details",
    "bus_seats",
    "total_amount",
    "trip_info",
    "seats_locked",
    "showModal"
  ]),
  methods: {
    ...mapActions([
      "vehicleSelected",
      "showLoader",
      "hideLoader",
      "getTripInfo",
      "getLockSeats",
      "hideModal",
      "showTheModal"
    ]),
    //Show onward Trip
    showOnward() {
      //Show first page
      this.showAppTripFilter = true;
      this.showAppBusResults = true;

      //Hide other pages
      this.showAppBusSeats = false;
      this.showAppPassangerForm = false;
      this.showAppPassangerForm = false;
      this.showAppTripReview = false;
      this.showAppPayment = false;

      //Hide Return Pages
      this.showAppReturnTripFilter = false;
      this.showAppReturnBusResults = false;
      this.showAppReturnBusSeats = false;
    },
    showReturn() {
      //Show Return Pages
      this.showAppReturnTripFilter = true;
      this.showAppReturnBusResults = true;

      //Hide other pages
      this.showAppTripFilter = false;
      this.showAppBusResults = false;
      this.showAppBusSeats = false;
      this.showAppPassangerForm = false;
      this.showAppPassangerForm = false;
      this.showAppTripReview = false;
      this.showAppPayment = false;

      //Hide Return Pages
      this.showAppReturnBusSeats = false;
    },
    busSelectedEvent: function(bus, is_return) {
      //Update Schedule Id, Boarding & Picking points
      this.vehicleSelected({ bus: bus, is_return: is_return });

      let self = this;

      //scroll to top
      window.scrollTo(0, 0);

      //hide the filter component
      self.showAppTripFilter = false;
      //hide the bus component
      self.showAppBusResults = false;
      //hide the filter component
      self.showAppReturnTripFilter = false;
      //hide the bus component
      self.showAppReturnBusResults = false;

      if (!is_return) {
        //show the bus component
        self.showAppBusSeats = true;
        //show the bus component
        self.showAppReturnBusSeats = false;
      } else {
        //show the bus component
        self.showAppReturnBusSeats = true;
        //show the bus component
        self.showAppBusSeats = false;
      }
    },
    busSeatSelectedEvent: function(
      boarding_point,
      dropping_point,
      selected_seats
    ) {
      //Check if details have been filled
      if (boarding_point == "") {
        //Alert
        izitoast.warning({
          title: "Caution",
          message: "Kindly fill the boarding point"
        });
      } else if (dropping_point == "") {
        //Alert
        izitoast.warning({
          title: "Caution",
          message: "Kindly fill the dropping point"
        });
      } else if (selected_seats.length < 1) {
        //Alert
        izitoast.warning({
          title: "Caution",
          message: "Kindly select a seat"
        });
      } else {
        //scroll to top
        window.scrollTo(0, 0);
        //Show Modal
        this.showTheModal();
      }
    },
    modalClosedEvent: function() {
      let self = this;

      //scroll to top
      window.scrollTo(0, 0);

      //hide bus seat component
      self.showAppBusSeats = false;

      //hide return bus seat component
      self.showAppReturnBusSeats = false;

      //hide modal componenet
      self.hideModal();

      //show passanger form componenet
      self.showAppPassangerForm = true;
    },
    passangerFormSubmittedEvent: function() {
      //Show Loader
      this.showLoader();

      this.getTripInfo();

      //Ficha

      //Hide Loader
      //this.hideLoader();

      //let self = this;

      //scroll to top
      //window.scrollTo(0, 0);

      //hide passanger details
      // self.showAppPassangerForm = false;

      //show review
      //self.showAppTripReview = true;

      //Ficha

      //Submit data request
      this.$http
        .post("http://193.200.241.91:8000/api/v2/tickets", this.trip_info)
        .then(
          response => {
            console.log(response);

            //Hide Loader
            this.hideLoader();

            let self = this;

            //scroll to top
            window.scrollTo(0, 0);

            //hide passanger details
            self.showAppPassangerForm = false;

            //show review
            self.showAppTripReview = true;
          },
          error => {
            console.log(error);
            //Hide Loader
            this.hideLoader();

            //Alert
            izitoast.error({
              title: "Error",
              message: "Could not complete request"
            });
          }
        );
    },

    tripReviewEvent: function() {
      let self = this;

      //scroll to top
      window.scrollTo(0, 0);

      //hide passanger form component
      self.showAppTripReview = false;

      //show payemnet component
      self.showAppPayment = true;
    },
    filterRequest: function(event) {
      event.preventDefault();
    },
    seatSelected: function(seat_) {
      console.log(seat_);
    },
    getSelectedBusSeats: function() {
      this.showAppBusSeats = true;
    }
  },
  mounted() {
    let self = this;

    //scroll to top
    window.scrollTo(0, 0);

    //ProgressBar Demo
    self.showAppLoader = true;
    setTimeout(() => {
      self.showAppLoader = false;
    }, 2000);
  }
};
</script>
