<template>
  <div class="booking-form-wrapper flexbox p-1 p-t-3 pos-relative zindex-1 w-40 mobile-w-100">
      <!-- Loader -->
      <div
        class="loader bus-progress-loader flexbox align-items-center justify-center zindex-10"
        v-show="show_Loader"
      >
        <div class="text-primary">
          <i class="fas fa-spinner fa-pulse fa-5x"></i>
        </div>
      </div>
      <!--Loader-->
    
    <div class="form-container mr-l-1 w-80 mobile-m-l-0 mobile-p-1 mobile-w-100 mobile-mr-l-0">
      <form @submit.prevent="getSubRequest({town_from: trip.town_from, town_to: trip.town_to, travelling_on: trip.travelling_on, is_return: false})">
        <div class="form-header">
          <h5 class="wight-semibold text-white mr-0 font-semibold">Experience travel in style</h5>
          <h2
            class="title text-white mr-0 mr-t-sm mr-b-1 font-bold font-xl mobile-font"
          >Book a ticket now</h2>
        </div>

        <div class="form-body">
          <div class="form-group">
            <label class="sr-only" id>Leaving From</label>
            <v-select
              v-model="trip.town_from"
              class="modern-selector"
              placeholder="Leaving From"
              label="town_name"
              :options="all_towns"
            ></v-select>
          </div>

          <div class="form-group">
            <label class="sr-only" id>Travelling To</label>
            <v-select
              v-model="trip.town_to"
              class="modern-selector"
              placeholder="Travelling To"
              label="town_name"
              :options="all_towns"
            ></v-select>
          </div>

          <div class="form-group">
            <label class="sr-only">Travelling On</label>
            <DatePicker
              v-model="trip.travelling_on"
              :min-date="new Date()"
              :max-date="maxTravelDate"
              :input-props="{
                class: 'vc-calendar-custom',
                placeholder: 'Travelling On',
                readonly: true
              }"
            />
          </div>

          <div class="form-group">
            <label class="sr-only">Returning On</label>
            <DatePicker
              v-model="trip.returning_on"
              :min-date="new Date()"
              :max-date="maxTravelDate"
              :input-props="{
                class: 'vc-calendar-custom',
                placeholder: 'Returning On (Optional)',
                readonly: true
              }"
            />
          </div>

          <!-- Show form feedback here -->
          <div
            class="form-group form-message text-white text-center hidden"
          >Enter some form feedback here or error message</div>
          <!-- Show form feedback here -->

          <!-- Submit Form -->
          <div class="form-group">
            <button class="btn btn-submit mr-t-sm">SEARCH AVAILABLE BUSES</button>
          </div>
          <!-- /Submit Form -->
        </div>
      </form>
    </div>
  </div>
</template>

<script>
/*Vuex Data & Actions*/
import { mapGetters, mapActions } from "vuex";

/* Max date, 16 days from today */
let getMaxTravelDate = () => {
  let today = new Date();
  let tomorrow = new Date(today);
  return tomorrow.setDate(tomorrow.getDate() + 16);
};

export default {
  name: "BookingForm",
  data() {
    return {
      travelDate: null,
      maxTravelDate: getMaxTravelDate(),
      formData: {
        to: "",
        from: ""
      }
    };
  },
  methods: {
    ...mapActions(["fetchTowns","getSubRequest"]),
  },
  computed: mapGetters(["all_towns", "trip", "show_Loader"]),
  created() {
    /*Get all towns through the API*/
    this.fetchTowns();
  },
  mounted() {
    console.log("Loader");
  }
};
</script>
